@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* download documents */
.file {
  $file-icon-size: 42px;
  @include display-inline-block-top;
  min-height: $file-icon-size;
  padding: 0.5em;
  @include font-size(16px, 20px);
  color: $text-color;
  text-decoration: none;

  @include clear;

  svg {
    float: left;
    @include size($file-icon-size); }

  & > span, & > em {
    display: block;
    margin-left: #{$file-icon-size};
    padding-left: 1em; }

  & > em {
    display: block;
    font-size: 1.4rem;
    color: $text-color-light; }

  .no-touchevents &:hover {
    background-color: $base-color;

    & > em {
      color: $bg-color; } } }

.st-bg {
  fill: #e2e5e7; }

.st-corner {
  fill: #b0b7bd; }

.st-shadow {
  fill: #cad1d8; }

.st-text {
  fill: #fff; }

/* download documents end */
