@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* helper classes */
.mark {
  color: $mark-color; }

.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.float-left {
  float: left; }

.float-right {
  float: right; }

.push-auto {
  margin-left: auto;
  margin-right: auto; }

.nobr {
  white-space: nowrap; }

.hide {
  display: none !important; }

.hide-text {
  @include hide-text; }

.desktop-only {
  @include media('<tab') {
    display: none; } }

.devices-only {
  @include media('>=tab') {
    display: none; } }

.mobile-only {
  @include media('>=mob') {
    display: none; } }

/* ==================================== */
/* ============== HEADER ============== */
/* ==================================== */
.header {
  background-color: rgba(lightgreen, 0.8); }

.header-container {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 5; }

/* logo */
.logo {
  flex-shrink: 0;
  position: relative;
  overflow: hidden;
  width: 85px;
  height: 85px;
  margin: 0 2rem 0 0;
  background: url(../../img/logo.svg) no-repeat;
  background-size: contain;
  text-indent: -9999px;
  z-index: 5;

  a, & > span {
    display: block;
    height: 100%; } }

/* logo end */

/* ==================================== */
/* ============ NAVIGATION ============ */
/* ==================================== */
.nav {
  position: relative; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  @include clear;

  li {
    position: relative;

    .nav__angle {
      display: none; }

    &.has-drop {
      padding-right: 2rem;

      & > .nav__tab > .nav__angle {
        display: block; }

      .nav__angle {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translate(0, -50%);
        line-height: 0;
        pointer-events: none; } } }

  a {
    display: block; }

  & > li {
    float: left;
    margin-right: 2rem; } }

.nav__drop {
  position: absolute;
  left: 0;
  top: 100%;
  background-color: white;
  border: 1px solid $border-color;
  padding: 2rem;
  width: $layout-page-min-width;
  opacity: 0;
  visibility: hidden;
  z-index: 10;

  li:hover > & {
    opacity: 1;
    visibility: visible; }

  li {
    margin-bottom: 1rem;
    font-size: 1.4rem;
    line-height: 1.5; }

  .nav__drop {
    top: 0;
    left: 100%; } }


/* ==================================== */
/* ============= FOOTER =============== */
/* ==================================== */
.footer {
  position: relative;
  //z-index: 5

  padding-top: 2rem;
  padding-bottom: 2rem;
  background-color: rgba(lightgreen, 0.8); }

/* footer bottom */
.footer-bottom {
  position: relative;
  // обрезать вытянутый маржинами по горизонтале дочерний элемент
  overflow: hidden; }

.footer-bottom__holder {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  @include margin-horizontal(-#{round(nth($grid-space, 2)/2)});
  margin-bottom: -#{round(nth($grid-space, 1)/2)}; }

.footer-bottom__col {
  text-align: center;
  @include padding-horizontal(round(nth($grid-space, 2)/2));
  margin-bottom: #{round(nth($grid-space, 1)/2)};
  // для браузеров не поддерживающих display: flex
  display: inline-block;
  vertical-align: top;
  width: calc(33.33% - 5px);

  @include media('<mob') {
    min-width: 100%; }

  &:first-child {
    @include media('>=mob') {
      text-align: left; } }

  &:last-child {
    @include media('>=mob') {
      text-align: right; } }

  a {
    color: inherit;
    text-decoration: none;

    .no-touchevents &:hover {
      text-decoration: underline; } } }

/* footer bottom end */

/* developer */
.developer {
  display: inline-block;

  a {
    text-decoration: none;

    .no-touchevents &:hover {
      text-decoration: none; } } }

.developer__label {
  @include media('<mob') {
    display: block; }

  i {
    @include media('<mob') {
      display: none; } } }

.developer__logo {
  @include display-inline-block-top;
  position: relative;
  top: 5px;
  padding-right: 3px;
  @include size(154px, 20px);
  text-align: left;
  text-indent: -9999px;

  &::before, &::after {
    @include pseudo();
    @include position(0, 0);
    @include size(149px, 16px); }

  &::before {
    @include pseudo();
    @include position(4px, 4px);
    background: url('data:image/png;base64,');
    background-size: 2px; }

  //.no-touchevents a:hover
  //transform: translate(0, 1px)
  //filter: blur(2px)
  //opacity: 0.6

  &::after {
    @include pseudo();
    background: #00ef85 url('data:image/png;base64,') no-repeat center center;
    background-size: auto 16px;

    .no-touchevents a:hover & {
      transform: translate(0, -1px); } } }

/* developer end */

/* ==================================== */
/* ============== MAIN ================ */
/* ==================================== */
.main {
  position: relative; }

.content {
  background-color: rgba(whitesmoke, 0.8); }
